  .pace-activity {
    position: absolute;
    width: 100%;
    height: 28px;
    z-index: 2001;
    box-shadow: inset 0 0 0 2px #f1d204, inset 0 0 0 7px #FFF;
    border-radius: 10px; }
  &.pace-inactive {
    display: none; } }

.pace-overlay {
  width: 100%;
  height: 100%;
  background: linear-gradient(to top,#111 0,#333 50%) !important;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  transition: opacity .1s ease; }

body {
  // .bottom-header .container .row > *,
  .header-navigation ul,
  .show-after-pace {
    > * {
      transform: translate(0, -0) scale(.95);
      opacity: 0;
      $time: 0s;
      $i: 0;
      @for $i from 0 through 20 {
        &:nth-child(#{$i}) {
          transition: transform 1s ease $time, opacity 1s ease $time; }
        $time: $time + 0.1; } } }

  &.pace-done {
    .header-navigation ul,
    .show-after-pace {
      > * {
        transform: translate(0,0) scale(1);
        opacity: 1 !important; } }
    .pace-overlay {
      opacity: 0;
      pointer-events: none; } } }
